@use '../../00-base/init' as *;

.a-button {
  /**
    * @tokens Buttons
    * @presenter Color
  */

  --ctek-button-color: var(--ctek-global-color-gray-100);

  // background
  --ctek-button-color-background: var(--ctek-button-color);
  --ctek-button-color-background-hover: var(--ctek-global-color-gray-200);
  --ctek-button-color-background-active: var(--ctek-global-color-gray-400);
  --ctek-button-color-background-disabled: var(--ctek-global-color-gray-200);

  // border
  --ctek-button-color-border: var(--ctek-button-color);
  --ctek-button-color-border-hover: transparent;
  --ctek-button-color-border-active: transparent;
  --ctek-button-color-border-disabled: var(--ctek-global-color-gray-200);
  --ctek-button-border-style: solid;
  --ctek-button-border-width: var(--ctek-global-border-size-2);
  --ctek-button-border-radius: var(--ctek-global-radius-2);

  // text
  --ctek-button-color-text: var(--ctek-button-color);
  --ctek-button-color-text-hover: var(--ctek-button-color-text);
  --ctek-button-color-text-active: var(--ctek-button-color-text);
  --ctek-button-color-text-diabled: var(--ctek-global-color-gray-200);

  // font
  --ctek-button-ff: var(--ctek-global-ff-secondary);
  --ctek-button-fs: var(--ctek-global-fs-1);
  --ctek-button-fw: var(--ctek-global-fw-semibold);

  // icon
  --ctek-button-icon-order: 0;
  --ctek-button-icon-space-left: 0;
  --ctek-button-icon-space-right: var(--ctek-global-space-1);
  --ctek-button-icon-space: 0 var(--ctek-button-icon-space-right) 0 var(--ctek-button-icon-space-left);
  --ctek-button-color-svg-fill: var(--ctek-button-color-text);
  --ctek-button-color-svg-fill-hover: var(--ctek-button-color-text-hover);

  // space
  --ctek-button-space-block: var(--ctek-global-space-105);
  --ctek-button-space-inline: var(--ctek-global-space-2);
  --ctek-button-space: var(--ctek-button-space-block) var(--ctek-button-space-inline);

  /**
    * @tokens-end
  */

  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: var(--ctek-button-space);
  overflow: hidden;
  color: var(--ctek-button-color-text);
  font-weight: var(--ctek-button-fw);
  font-size: var(--ctek-button-fs);
  font-family: var(--ctek-button-ff);
  line-height: var(--ctek-global-lh-none);
  text-decoration: none;
  background-color: var(--ctek-button-color-background);
  background-image: none;
  border-color: var(--ctek-button-color-border);
  border-style: var(--ctek-button-border-style);
  border-width: var(--ctek-button-border-width);
  border-radius: var(--ctek-button-border-radius);
  cursor: pointer;

  &:hover,
  &:focus {
    color: var(--ctek-button-color-text-hover);
    background-color: var(--ctek-button-color-background-hover);
    border-color: var(--ctek-button-color-border-hover);

    .a-button__icon {
      margin: var(--ctek-button-icon-space);

      svg {
        width: var(--ctek-button-fs);
        height: var(--ctek-button-fs);
        fill: var(--ctek-button-color-svg-fill-hover);
      }
    }
  }

  &:active {
    color: var(--ctek-button-color-text-active);
    background-color: var(--ctek-button-color-background-active);
    border-color: var(--ctek-button-color-border-active);
  }

  &[disabled] {
    color: var(--ctek-button-color-text-disabled);
    background-color: var(--ctek-button-color-background-disabled);
    border-color: var(--ctek-button-color-border-disabled);
  }

  .a-button__icon {
    order: var(--ctek-button-icon-order);
    margin: var(--ctek-button-icon-space);

    svg {
      width: var(--ctek-button-fs);
      height: var(--ctek-button-fs);
      fill: var(--ctek-button-color-svg-fill);
    }
  }

  .a-button__text,
  .a-button__icon {
    display: inline-flex;
    align-items: center;
  }

  &.a-button--primary {
    --ctek-button-color: var(--ctek-global-color-primary);
    --ctek-button-color-background-hover: var(--ctek-global-color-black);
    --ctek-button-color-text: var(--ctek-global-color-white);
    --ctek-button-color-text-hover: var(--ctek-global-color-white);
    --ctek-button-color-text-active: var(--ctek-global-color-white);
    --ctek-button-color-background-active: var(--ctek-global-color-gray-800);
  }

  &.a-button--secondary {
    --ctek-button-color: var(--ctek-global-color-secondary);
    --ctek-button-color-text: var(--ctek-global-color-black);
    --ctek-button-color-text-hover: var(--ctek-global-color-white);
    --ctek-button-color-background-hover: var(--ctek-global-color-black);
    --ctek-button-color-text-active: var(--ctek-global-color-white);
    --ctek-button-color-background-active: var(--ctek-global-color-gray-800);
  }

  &.a-button--primary--outline {
    --ctek-button-color: var(--ctek-global-color-primary);
    --ctek-button-color-background: transparent;
    --ctek-button-color-text-hover: var(--ctek-global-color-white);
    --ctek-button-color-background-hover: var(--ctek-button-color);
    --ctek-button-color-text-active: var(--ctek-global-color-white);
    --ctek-button-color-background-active: var(--ctek-global-color-primary-darker);
  }

  &.a-button--secondary--outline {
    --ctek-button-color: var(--ctek-global-color-secondary);
    --ctek-button-color-background: transparent;
    --ctek-button-color-text-hover: var(--ctek-global-color-white);
    --ctek-button-color-background-hover: var(--ctek-button-color);
    --ctek-button-color-text-active: var(--ctek-global-color-white);
    --ctek-button-color-background-active: var(--ctek-global-color-secondary-darker);
  }

  &.a-button--sm {
    --ctek-button-fs: var(--ctek-global-fs-0);
    --ctek-button-space-block: var(--ctek-global-space-05);
    --ctek-button-space-inline: var(--ctek-global-space-1);
    --ctek-button-border-width: var(--ctek-global-border-size-1);
    --ctek-button-border-radius: var(--ctek-global-radius-1);
  }

  &.a-button--md {
    --ctek-button-fs: var(--ctek-global-fs-3);
    --ctek-button-space-block: var(--ctek-global-space-1);
    --ctek-button-space-inline: var(--ctek-global-space-2);
  }

  &.a-button--lg {
    --ctek-button-fs: var(--ctek-global-fs-5);
    --ctek-button-space-block: var(--ctek-global-space-2);
    --ctek-button-space-inline: var(--ctek-global-space-3);
  }

  &.a-button--block {
    display: block;
    width: 100%;
  }

  &.a-button--icon-only {

    .a-button__text {

      @include visually-hidden;
    }

    .a-button__icon {
      margin: 0;
    }
  }

  &.a-button--icon-position--right {
    --ctek-button-icon-order: 2;
    --ctek-button-icon-space-left: var(--ctek-global-space-1);
    --ctek-button-icon-space-right: 0;
  }
}
